@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;800&display=swap");

.m-only {
  @media (min-width: 769px) {
    display: none !important;
  }
}

.d-only {
  @media (max-width: 768px) {
    display: none !important;
  }
}

.nunito {
  font-family: "Nunito", sans-serif;
}

.open-sans {
  font-family: "Open Sans", sans-serif;
}

.font-200 {
  font-weight: 200;
}

.font-300 {
  font-weight: 300;
}

.font-400 {
  font-weight: 400;
}

.font-600 {
  font-weight: 600;
}

.font-800 {
  font-weight: 800;
}

.font-black {
  color: black;
}

.font-purple {
  color: #6930c3;
}

.font-blue {
  color: #5e60ce;
}

.transition-all {
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}

.font-gradient {
  background: -webkit-linear-gradient(#7400b8, #80ffdb);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

p {
  padding: 0;
  margin: 0;
}

.purple-a {
  color: #6930c3;

  &:hover {
    color: rgba(105, 48, 195, 0.7);
  }
}

.full-body {
  width: 100vw !important;
  max-width: 100vw !important;
  font-family: "Open Sans", sans-serif;

  overflow-x: hidden !important;
}

h1 {
  font-size: 4rem !important;
}

section {
  width: 100vw !important;
}

.navbar-full {
  position: fixed;
  width: 100% !important;
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;
  z-index: 10;
  .navbar-body {
    padding: 0;
    margin: 0;
    background: rgb(129, 255, 219);

    -webkit-box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);
    -moz-box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);
    box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);

    background-color: #6930c3;
    border-radius: 1.5rem;

    color: rgb(129, 255, 219);

    .nav-mobile-links {
      max-height: 0 !important;
      overflow: hidden !important;

      a {
        color: rgb(129, 255, 219) !important;
        font-size: 1.25rem;
      }

      &.nav-expanded {
        max-height: 50vh !important;
      }
    }

    .mobile-arrow {
      max-width: 30px;

      g {
        fill: orange !important;
      }

      &.upside-down {
        transform: rotate(180deg);
      }
    }

    .font-purple {
      color: rgb(129, 255, 219) !important;
    }

    p {
      margin: 0;
      padding: 0;
    }

    .col {
      display: flex;
      align-items: center;

      p {
        font-size: 2rem;
      }
    }

    .col-auto {
      display: flex;
      align-items: center;

      a {
        font-weight: 600;

        cursor: pointer;

        color: rgb(129, 255, 219) !important;
      }
    }
  }
}

.research-body {
  width: 100vw !important;
  max-width: 100vw !important;
  overflow-x: hidden !important;

  padding-top: 8rem;

  p {
    margin: 0;
    padding: 0;
  }

  .img-col {
    max-width: 300px !important;

    @media (max-width: 768px) {
      margin-top: 2rem;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .no-a {
    text-decoration: none !important;

    color: #6930c3 !important;
  }
}

.home-body {
  width: 100vw !important;
  max-width: 100vw !important;

  overflow-x: hidden;

  section.schedule {
    .schedule-row {
      width: 100% !important;
      background-color: red;

      .col-md-auto {
        max-width: 25% !important;
      }
    }
  }

  section.contact {
    .contact-row {
      background: rgb(129, 255, 219);

      background: linear-gradient(45deg, #80ffdb 0%, #48bfe3 100%);

      border-radius: 1.5rem;

      -webkit-box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);
      -moz-box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);
      box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);

      .hours {
        display: flex;
        align-items: center;
      }

      .col-md {
        @media (max-width: 1000px) {
          min-width: 100% !important;

          img {
            margin-top: 3rem;
          }
        }
      }
    }

    @media (max-width: 768px) {
      .contact-row {
        margin-left: 0.5rem !important;
        margin-right: 0.5rem !important;

        h1 {
          font-size: 3rem !important;
        }
      }
    }
  }

  section.about {
    h1 {
      font-size: 3rem !important;
    }
    .card-row {
      .col-md {
        padding: 0;

        -webkit-box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);
        -moz-box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);
        box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);

        p {
          padding: 0;
          margin: 0;
        }

        background: rgb(129, 255, 219);

        background: linear-gradient(45deg, #80ffdb 0%, #48bfe3 100%);
        border-radius: 1.5rem;

        cursor: pointer;

        .title-text {
          height: 15vh;
          align-items: center;
          text-align: center !important;
          padding: 2rem;

          border-radius: 1.5rem;

          background-color: rgba(255, 255, 255, 0.3);

          h3 {
            font-size: 1.25rem;
            padding: 0;
            margin: 0;
          }
        }

        .p-text {
          height: 40vh;
          align-items: center;
          padding: 2rem;
        }

        &:hover {
          .title-text {
            background-color: rgba(255, 255, 255, 0.5);

            h3 {
              color: black !important;
            }
          }
        }
      }

      @media (max-width: 1000px) {
        .col-md {
          min-width: 100% !important;
          margin-left: 0 !important;
          margin-right: 0 !important;

          &.my-md-0 {
            margin-top: 3rem !important;
            margin-bottom: 3rem !important;
          }

          .title-text {
            height: unset !important;
          }
          .p-text {
            height: unset !important;
          }
        }
      }

      @media (max-width: 768px) {
        padding-left: 1rem !important;
        padding-right: 1rem !important;
      }
    }
    .k-row {
      .col-md {
        display: flex;
        align-items: center;

        &.img-k {
          width: auto !important;
          max-width: 35%;
        }
      }

      @media (max-width: 768px) {
        flex-direction: column-reverse;

        .img-k {
          max-width: unset !important;

          img {
            width: 70% !important;
            margin: auto;
            margin-top: 2rem;
          }
        }
      }
    }
    .row-bio {
      .col-md {
        display: flex;
        align-items: center;
      }
      .image-col {
        div {
          &.w-100 {
          }
        }
      }
    }
  }

  section.intro {
    width: 100vw !important;
    padding-top: 8rem;
    padding-left: 2rem;

    .buttons-div {
      .col {
        border-color: #6930c3;
        //border-color: rgb(129, 255, 219);
        border-style: solid;
        border-width: 2px;

        cursor: pointer;

        text-align: center;
        display: flex;
        align-items: center;

        &.two {
          background-color: #6930c3;
          p {
            color: #80ffdb !important;
          }
        }

        .inner-b {
          text-align: center;
          p {
            margin: 0;
            padding: 0;
          }
        }

        &.w30 {
          overflow: hidden;

          -webkit-box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);
          -moz-box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);
          box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);

          background: linear-gradient(
            90deg,
            rgba(129, 255, 219, 1) 0%,
            #75cad7 100%
          );

          &.two {
            background: linear-gradient(90deg, #75cad7 0%, #6a96d2 100%);
          }

          &.three {
            background: linear-gradient(90deg, #6a96d2 0%, #5e60ce 100%);
          }
        }

        border-radius: 1.25rem;
      }
    }

    .intro-div {
      background: rgb(129, 255, 219);
      width: 100% !important;

      overflow: hidden;

      -webkit-box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);
      -moz-box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);
      box-shadow: 10px 10px 27px -3px rgba(0, 0, 0, 0.47);

      background: linear-gradient(
        90deg,
        rgba(129, 255, 219, 1) 0%,
        #5e60ce 100%
      );

      border-radius: 1.5rem;

      .col-auto {
        max-width: 65% !important;
      }

      .col {
        &.d-flex {
          align-items: center;
        }

        &.img-flex {
          position: relative;

          img {
            position: absolute;
            bottom: 0;
            max-width: 40vw;
          }
        }

        .row {
          &.intro-buttons {
            a {
              background-color: transparent;

              cursor: pointer;

              -webkit-box-shadow: inset 0px 0px 13px 0px rgba(0, 0, 0, 0.47);
              -moz-box-shadow: inset 0px 0px 13px 0px rgba(0, 0, 0, 0.47);
              box-shadow: inset 0px 0px 13px 0px rgba(0, 0, 0, 0.47);

              border-radius: 1rem;
            }
          }
        }
      }
    }

    @media (max-width: 1000px) {
      .intro-div {
        background: linear-gradient(
          90deg,
          rgba(129, 255, 219, 1) 0%,
          #4ea8de 100%
        );
        .col {
          min-width: 100% !important;
          width: 100% !important;

          &.take-col {
            padding-left: 1rem !important;

            text-align: center;
          }

          &.img-flex {
            padding-top: 0 !important;
            text-align: center;
            img {
              position: relative;
            }
          }
        }
      }
    }

    @media (max-width: 768px) {
      padding-left: 0 !important;
      padding-right: 0 !important;

      .intro-div {
        margin: 0 !important;

        .font-800 {
          font-size: 3rem !important;
        }
        .font-600 {
          font-size: 1.25rem !important;
        }
      }

      .buttons-div {
        .col {
          min-width: 100% !important;
          margin-left: 1rem !important;

          &.two {
            margin-top: 2rem !important;
          }

          .inner-b {
          }
        }
      }
    }
  }
}
